import './Contact.js'
import {useState} from 'react';


function Form(){
    const[name,setname] = useState('');
    const[addr,setaddr] = useState('');
    const[gend,setgend] = useState('');
function submitHandler(event){
    event.preventDefault();
localStorage.name = name;
localStorage.addr = addr;
localStorage.gend = gend;

}

    return(
        <>
        
        <div className='Frame'>
            <br/>
            <form onSubmit={submitHandler}>
            
            <label>Name: </label>
            <input className='Link' type="text" value={name} 
            onChange={(e)=>setname(e.target.value)} /><br/><br/>
            <label>Address: </label>
            <textarea className='Link2' value={addr} 
            onChange={(e)=>setaddr(e.target.value)}></textarea>
            <br/><br/>
            <label>Gender: </label>
            <select className='Link3' value={gend} 
            onChange={(e)=>setgend(e.target.value)}>
            <option value="Select">Select</option>
                <option value="Male">Male</option>
                <option value="Female">Female</option>
            </select><br/><br/>
            <input type="submit" value="submit form"/>
        </form><br/>
        </div><br/>
<br/>

            
            </>
    )
}

export default Form;