import {useState} from 'react';

function Dogs(){

const[img,setimg] = useState()
 

const axios = require('axios');


axios.get('https://dog.ceo/api/breeds/image/random').
then((response)=>{
    setimg (response.data.message)

   
})


    return(

        < >

        <img alt='' src={img}/>

        </>
    )
}

export default Dogs;